









import Vue from 'vue';
import './Form.scss';

export default Vue.extend({
    name: "Form-filter",
    data() {
        return {
            form: {
                title: '' as string
            }
        }
    },
    computed: {
      data(): Array<object> {
          return this.$store.getters.data;
      }
    },
    watch: {
        'form.title'() {
            if (!this.form.title) {
                this.$store.dispatch('clearFilterInput');
            }
        }
    },
    methods: {
        onSubmit(evt: any): void {
            evt.preventDefault();

            this.$store.dispatch('changeCurrentPage', 1);
            this.$store.dispatch('filterData', this.form.title);
        }
    }
})
