



































import Vue from 'vue';
import './Table.scss';
import FieldTitle from '@/components/field-title/Field-title.vue';
import Pagination from '@/components/pagination/Pagination.vue';

export default Vue.extend({
    name: "Table",
    data() {
        return {
            fields: [
                {
                    key: 'id',
                    label: 'ID',
                    sortable: true
                },
                {
                    key: 'firstName',
                    label: 'First Name',
                    sortable: true
                },
                {
                    key: 'lastName',
                    label: 'Last Name',
                    sortable: true
                },
                {
                    key: 'phone',
                    label: 'Phone',
                    sortable: false
                },
                {
                    key: 'email',
                    label: 'Email',
                    sortable: false
                }
            ] as Array<object>
        }
    },
    computed: {
        data(): Array<object> {
            return this.$store.getters.data;
        },
        perPage(): number {
            return this.$store.getters.perPage;
        },
        currentPage(): number {
            return this.$store.getters.currentPage;
        },
        pagesCount(): number {
            const length = this.data.length;

            let count = length / this.perPage;

            if (count > 0 && count < 1) {
                return 1;
            }

            if (length % this.perPage) {
                count += 1;
            }

            return count;
        },
        pages(): Array<number> {
            const pages = [];

            for (let i = 1; i <= this.pagesCount; i++) {
                pages.push(i);
            }

            return pages;
        },
    },
    methods: {
      onClickChangePage({type, page}: any): void {
          switch (type) {
              case `prev`:
                  this.$store.dispatch('changeCurrentPage', this.currentPage - 1);
                  break;

              case 'next':
                  this.$store.dispatch('changeCurrentPage', this.currentPage + 1);
                  break;

              case 'first':
                  this.$store.dispatch('changeCurrentPage', 1);
                  break;

              case 'last':
                  this.$store.dispatch('changeCurrentPage', Math.trunc(this.pagesCount));
                  break;

              default:
                  this.$store.dispatch('changeCurrentPage', page);
                  break;
          }
      },
        onClickChooseUser(userId: number) {
          this.$store.dispatch('getChosenUser', userId);

          setTimeout(() => {
              window.scrollTo({
                  top: document.body.scrollHeight,
                  behavior: 'smooth'
              });
          }, 100);
        },
    },
    components: {
        FieldTitle,
        Pagination
    }
})
